import React, {useCallback, useRef} from 'react';
import {dismissEvent} from '../../utils';

const ORIGIN_TO_IMG = {
	ynet: 'https://m.ynet.co.il/content/images/icons/site/big_ynet_logo.png?v=20200317',
	mako: 'https://img.mako.co.il/2013/08/15/mako-logo_i.jpg',
	walla: 'data:image/png;base64,',
};
const NewsItem = ({ uuid, title, link, source, dateTimestamp }) => {
	const itemRef = useRef({});
	const imgRef = useRef({});

	const goToLink = useCallback(() => {
		window.open(link, '_blank');
	}, [link]);

	const onRemove = useCallback((e) => {
		dismissEvent([imgRef, itemRef], {date: dateTimestamp, type: 'news', id: uuid});
		e.stopPropagation();
	}, [itemRef, imgRef, dateTimestamp, uuid]);
	return (
		<div className="item news-item" ref={itemRef} onClick={goToLink}>
			<img className={`news-source-img ${source}`} ref={imgRef} src={ORIGIN_TO_IMG[source]} alt={source} />
			<span className="text">{title}</span>
			<button className="material-icons" type="button" onClick={onRemove}>cancel</button>
		</div>
	);
};

export default NewsItem;
